	module.exports = {"data":{"markdownRemark":{"html":"<p>You can customize your program's response SLA times by configuring the number of business days. To customize your program's response SLA time: </p>\n<ol>\n<li>Go to <strong>Settings > Program > Response SLA</strong>. </li>\n<li>Input the target response SLA time in days for your program under the <strong>Target</strong> column. </li>\n</ol>\n<p>Keep in mind that:</p>\n<ul>\n<li>\n<p>It's recommended that you're targets fall within the Recommended and Limit times. </p>\n<p>Time | Recommended | Limit\n-------- | ----------- | -----\nTime to first response | 1 day | 5 days\nTime to triage | 2 days | 10 days\nTime to bounty | 1 day (after triage) | N/A\nTime to resolution | 30 days | N/A </p>\n</li>\n<li>\n<p>To edit your program's response SLA settings, you need to have program permissions. </p>\n</li>\n<li>\n<p>Modified targets will be applied to reports created after the modification. </p>\n</li>\n<li>\n<p>Reports created before modification will have previous SLA targets applied. </p>\n</li>\n</ul>\n<p><img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/programs/images/response-sla.png?raw=true\" alt=\"response sla\"></p>","frontmatter":{"path":"/programs/setting-response-slas","title":"Setting Response SLAs"}}},"pathContext":{}}

